import React from "react";
import styled, { css } from "styled-components";
import { VscSymbolMethod } from "react-icons/vsc";
import { AiFillMinusCircle } from "react-icons/ai";
import { IoMdAddCircle } from "react-icons/io";

import { ReasearchData } from "../Data/ResearchData";

import { useState } from "react";
import { Rotate } from "./Animation";

function Method({ title }) {
  // accordion fuction
  const [clicked, setClicked] = useState(null);
  const Toggle = (index) => {
    if (clicked === index) {
      return setClicked(null);
    }
    setClicked(index);
  };
  // accordion function end
  //
  return (
    <>
      <ColumnLeft>
        <Icons>
          <Methods />
          {title && <span>{title}</span>}
        </Icons>

        {ReasearchData.map((items, index) => {
          return (
            <>
              <Wrap key={index}>
                <span></span>
                <h3 to="/">{items.Question}</h3>
                <span onClick={() => Toggle(index)}>
                  {clicked === index ? <Plus /> : <Minus />}
                </span>
              </Wrap>

              {clicked === index ? (
                <Content>
                  <p>{items.answer}</p>
                </Content>
              ) : null}
            </>
          );
        })}
      </ColumnLeft>
    </>
  );
}
const Icons = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem 0;
  span {
    font-size: 20px;
    text-transform: capitalize;
  }
`;
const Methods = styled(VscSymbolMethod)`
  color: rgba(188, 31, 96, 0.5);
  font-size: 4rem;
`;
const ColumnLeft = styled.ul`
  display: flex;
  flex-direction: column;
  margin: 0 20px;
  padding: 0;

  @media (max-width: 768px) {
    padding: 0;
  }
`;

const Wrap = styled.div`
  background: white;
  padding: 5px 0px;
  align-items: center;
  justify-content: space-around;
  color: #00000;
  width: 100%;
  margin-bottom: 3rem;
  display: flex;
  flex-wrap: nowrap;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
    rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
  border-radius: 20px;
  cursor: pointer;
  margin-bottom: 5px;
  @media (max-width: 600px) {
  }

  span {
    margin-right: 10px;
  }
  h3 {
    font-size: 12px;
    font-weight: 100;
    width: 100%;
    margin: 0;
    text-align: center;
    @media (max-width: 500px) {
      font-size: 10px;
    }

    &:hover {
      padding-left: 10px;
      transition: 0.2s ease-in-out;
    }
  }
`;
const Content = styled.div`
  display: flex;
  background: white;
  align-items: center;
  justify-content: center;
  margin: 5px 0;

  p {
    margin: 0;
    font-size: 12px;
    line-height: 1.6;
    font-weight: 0;
    padding: 1rem;
    color: #242424;
  }
`;
const PlusMinus = css`
  color: #bc1f60;
  transform: rotate(0deg);
  font-size: 1.5rem;
  transition: 0.2s ease-in-out;
`;
const Plus = styled(AiFillMinusCircle)`
  ${PlusMinus};
`;
const Minus = styled(IoMdAddCircle)`
  ${PlusMinus};
`;

export default Method;
