import React, { useEffect, useState } from "react";
import { FiArrowUp } from "react-icons/fi";
import styled from "styled-components";
function Scroll() {
  const [visible, setVisible] = useState();
  const scrollTotop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const ToggleVisibility = () => {
    if (window.pageYOffset > 500) {
      setVisible(true);
    } else {
      setVisible(false);
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", ToggleVisibility);
  }, []);
  return (
    <ScrollBox>
      {visible && (
        <ScrollWrap onClick={scrollTotop}>
          <ScrollIcon />
        </ScrollWrap>
      )}
    </ScrollBox>
  );
}
const ScrollBox = styled.div`
  overflow: hidden;
`;
const ScrollWrap = styled.div`
  color: #fff;
  width: 50px;
  height: 50px;
  line-height: 30px;
  border-radius: 100%;
  font-size: 20px;
  position: fixed;
  right: 37px;
  bottom: 37px;
  cursor: pointer;
  background-color: #07a8ff;
  display: flex;
  align-items: center;
  z-index: 10;
  &:hover {
    background-color: #00bf6f;
  }
`;
const ScrollIcon = styled(FiArrowUp)`
  width: 30px;
  margin: 6px auto;
  display: block;
  vertical-align: middle;
  line-height: 27px;
  height: 47px;
  animation: TOponScroll linear 0.2s infinite;
`;

export default Scroll;
