import React, { useState } from "react";
import styled from "styled-components";
const User = ({ user }) => {
  // accordion fuction
  const [clicked, setClicked] = useState(null);
  const Toggle = (id) => {
    if (clicked === user.id) {
      return setClicked(null);
    }
    setClicked(user.id);
  };
  // accordion function end
  return (
    <>
      <Wrap onClick={Toggle}>
        <h3>{`Full Name: ${user.name.first} ${user.name.last}`}</h3>
      </Wrap>
      {clicked === user.id ? (
        <Content>
          <p>{`Phone: ${user.phone}`}</p>
        </Content>
      ) : null}
    </>
  );
};
const Wrap = styled.div`
  cursor: pointer;

  h3 {
    font-weight: 200;
    padding: 0.6rem;
    text-align: center;
    color: #065db2;
  }
`;
const Content = styled.div`
  width: 100%;
  background: white;
  text-align: center;
  P {
    color: #065db2;
  }
`;
export default User;
