import { Button } from "./Button";
import React from "react";
import styled from "styled-components";
import { HeroData } from "../Data/HeroData";
import { IoMdArrowRoundForward } from "react-icons/io";
import { IoArrowForward, IoArrowBack } from "react-icons/io5";
import { css } from "styled-components";
import { useState } from "react";
import { useRef } from "react";
import { useEffect } from "react";

function HeroSection({ slider }) {
  const [current, setCurrent] = useState(0);
  const length = slider.length;
  console.log(length);
  const Timeout = useRef(null);
  const NextSlider = () => {
    setCurrent((current) => (current === length - 1 ? 0 : current + 1));
    // console.log(current);
  };
  const PrevSlider = () => {
    setCurrent((current) => (current === 0 ? length - 1 : current - 1));
    // console.log(current);
  };
  useEffect(() => {
    const NextSlider = () => {
      setCurrent((current) => (current === length - 1 ? 0 : current + 1));
      // console.log(current);
    };
    Timeout.current = setTimeout(NextSlider, 3000);
    return function () {
      if (Timeout.current) {
        clearTimeout(Timeout.current);
      }
    };
  }, [length, current]);
  return (
    <Hero>
      <HeroContainer>
        {HeroData.map((item, index) => {
          return (
            <HeroSlide key={index}>
              {current === index && (
                <HeroSlider>
                  <HeroImage src={item.Image} />
                  <HeroContent>
                    <h1>{item.title}</h1>
                    <p>
                      Field of specialization: Experimental and numerical heat
                      transfer, Optimization in heat transfer, Inverse heat
                      transfer, Electronic cooling, Satellite Meteorology,
                      Numerical weather prediction and Radiance assimilation,
                      Climate change.
                    </p>
                    <Button to="/" Primary="true">
                      {item.btnLabel}
                      <Arrow />
                    </Button>
                  </HeroContent>
                </HeroSlider>
              )}
            </HeroSlide>
          );
        })}

        <Sliderbutton>
          <PrevArrow onClick={PrevSlider} />
          <NextArrow onClick={NextSlider} />
        </Sliderbutton>
      </HeroContainer>
    </Hero>
  );
}
const Hero = styled.section`
  height: 70vh;
  position: relative;
  overflow: hidden;
`;
const HeroContainer = styled.div`
  padding: 2rem 0;
  width: 100%;
  z-index: 5;
  height: 100%;
`;
const HeroSlide = styled.div``;
const HeroSlider = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  &:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.7) 0%,
      rgba(0, 0, 0, 0.6) 50%,
      rgba(0, 0, 0, 0.6) 100%
    );
    z-index: 3;
    opacity: 0.7;
    @media (max-width: 500px) {
      background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0.6) 70%,
        rgba(0, 0, 0, 0.4) 100%
      );
    }
  }
`;
const HeroImage = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;
const HeroContent = styled.div`
  position: absolute;
  color: #242424;
  backdrop-filter: blur(2px);
  top: 5rem;
  right: 5rem;
  max-width: 300px;
  padding: 4rem;
  z-index: 4;
  background: rgba(255, 255, 255, 0.5);

  @media (max-width: 768px) {
    right: 0rem;
    padding: 2rem;
    top: 5rem;
  }

  h1 {
    font-size: 30px;
    text-transform: uppercase;
    @media (max-width: 500px) {
      font-weight: 1000;
      border-bottom: 1px solid red;
    }
  }
  p {
    font-size: 15px;
    @media (max-width: 500px) {
      font-weight: 400;
      color: #000000;
    }
  }
`;
const Arrow = styled(IoMdArrowRoundForward)`
  margin-left: 0.3rem;
`;

const ArrowButton = css`
  width: 30px;
  height: 30px;
  color: #fff;
  cursor: pointer;
  background: #000d1a;
  border-radius: 50px;
  padding: 10px;
  margin-right: 1rem;
  user-select: none;
  transition: 0.3s;
  &:hover {
    background: #cd853f;
    transform: scale(1.05);
  }
  @media (max-width: 500px) {
    width: 15px;
    height: 15px;
  }
`;
const Sliderbutton = styled.div`
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 10px;
  z-index: 10;

  @media (max-width: 500px) {
    width: 100%;
    justify-content: space-between;
  }
`;
const PrevArrow = styled(IoArrowBack)`
  ${ArrowButton}
`;
const NextArrow = styled(IoArrowForward)`
  ${ArrowButton}
`;

export default HeroSection;
