import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { DropDownDataOne } from "./Data/DropDownData";
function DropDown({ DropIsopen }) {
  return (
    <DropDownList isOpen={DropIsopen}>
      {DropDownDataOne.map((items, index) => {
        <a key={index} href={items.path}>
          {items.title}
        </a>;
      })}
    </DropDownList>
  );
}
const DropDownList = styled.div`
  position: absolute;
  top: 50px;
  left: 20%;
  background: red;
  width: 200px;
  height: 150px;
  colorl: #fff;
  z-index: -1;
  display: ${({ isOpen }) => (isOpen ? "block" : "none")};
  a {
    color: white;
    z-index: 0;
  }
`;

// const DropDownContainer = styled.div``;

export default DropDown;
