import styled from "styled-components";
export const Button = styled.button`
  outline: none;
  border: 2px solid ${({ Primary }) => (Primary ? "#bc1f60" : "#fff")};
  background: ${({ Primary }) => (Primary ? "none" : "none")};
  color:   ${({ Primary }) => (Primary ? "#bc1f60" : "#fff")};
  width: 200px;
  height: 50px;
  padding: 0.5rem 0.5rem;
  border-radius: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;
  font-size: 1rem;
  font-weight: 100;
  text-transform: capitalize;
  position: relative;
  overflow: hidden;
  transition: 0.4ss transform ease-out;
  will-change: transform;
  z-index:0;


  &:after {
    position: absolute;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: ${({ Primary }) => (Primary ? "#bc1f60" : "white")}!important;
    left: 0;
    border-radius: 3rem;
    top: 0;
    transform: translate(-100%,0) rotate(10deg) ;
  transition: 0.2s transform ease-out;
   
    transform-origin: top left;
    z-index:-1;
  }
  &:hover {
    &:after {
      transform: translate(0,0)!important;
     
    }
   
    &:hover{
      border: 2px solid ${({ Primary }) =>
        Primary ? "transparent" : "transparent"};
    color :${({ Primary }) => (Primary ? "#fff" : "#242424")};
    transform: scale(1.05)!important;
    will-change: transform!important;
    }
    
`;
