import React from "react";
import RegionContent from "../RegionContent";
import styled from "styled-components";
import { BsLayersFill } from "react-icons/bs";
import { SiResearchgate } from "react-icons/si";
import { ImBooks } from "react-icons/im";
import { CgDisplayFullwidth } from "react-icons/cg";
import { Container } from "@material-ui/core";

function MainSection() {
  return (
    <Container>
      <MainContainer>
        <RegionContent title="recent papers" icon={<BsLayersFill />} isred />
        <RegionContent title="Presentation" icon={<CgDisplayFullwidth />} />
        <RegionContent title="research" tabistrue icon={<SiResearchgate />} />
        <RegionContent title="Books" icon={<ImBooks />} isred />
      </MainContainer>
    </Container>
  );
}
const MainContainer = styled.div`
  grid-template-columns: 0.6fr 0.4fr;
  grid-template-rows: 1fr 1fr;
  grid-row-gap: 50px;
  grid-column-gap: 100px;
  display: grid;
  height: 100%;
  margin-top: 5rem;

  @media (max-width: 1220px) {
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
`;
export default MainSection;
