import React from "react";
import styled from "styled-components";
import { YoutubeData } from "../Data/YoutubeData";

function YoutubeArea() {
  return (
    <YoutubeContainer>
      <h1>Video Lectures and Seminars</h1>
      <Youtubewrapper>
        {YoutubeData.map((items, index) => {
          return (
            <Youtubevideo key={index}>
              <iframe src={items.src}></iframe>
              <a href={items.src} target="_blank">
                {items.description}
              </a>
            </Youtubevideo>
          );
        })}
      </Youtubewrapper>
    </YoutubeContainer>
  );
}
const YoutubeContainer = styled.section`
  overflow: hidden;
  height: auto;
  padding: 1rem;
  h1 {
    text-align: center;
    margin-bottom: 2rem;
    font-weight: 100;
  }
`;
const Youtubewrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 1rem;
`;
const Youtubevideo = styled.div`
  display: grid;
  align-items: center;
  justify-content: center;

  iframe {
    border: none;
    outline: none;
  }
  a {
    margin: 15px 0;
    text-align: center;
    font-weight: 200;
    color: green;
    font-size: 15px;
    text-decoration: underline;
    cursor: pointer;
  }
`;

export default YoutubeArea;
