import React from "react";
import styled, { css } from "styled-components/macro";
import { FiMenu } from "react-icons/fi";
import { NavData } from "../Data/NavData";
import SearchIcon from "@material-ui/icons/Search";
import { Link } from "react-router-dom";
import { useState } from "react";
import SearchArea from "../Search";
import MobileDropDown from "./MobileDropDown";
import DropDown from "../DropDown";

function Navbar() {
  // menubar Toggle
  const [isOpen, setIsopen] = useState(false);
  const Toggle = () => {
    setIsopen(!isOpen);
  };
  // menubar Toggle End
  // navchange start
  const [Navbar, setNavbar] = useState(false);
  const changeNav = () => {
    if (window.scrollY >= 100) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };
  window.addEventListener("scroll", changeNav);
  //navchange end
  //search start
  const [SearchIsopen, SetSearchisOpen] = useState(false);

  const HandleSearch = () => {
    SetSearchisOpen(!SearchIsopen);
  };
  //search end
  //Dropdown function Start

  const [DropIsopen, SetDropisOpen] = useState(false);
  const HandleDropDown = () => {
    SetDropisOpen(!DropIsopen);
  };
  //Dropdown function end

  return (
    <Container navbar={Navbar}>
      <Logo>
        <h1>IIT madras</h1>
      </Logo>
      <Menubar onClick={Toggle} />
      <MobileDropDown isOpen={isOpen} Toggle={Toggle} />

      <NavMenu>
        {NavData.map((item, index) => (
          <>
            <NavLink navbar={Navbar} key={index} to={item.path}>
              <span>
                {item.title} <span onClick={HandleDropDown}>{item.icon}</span>
              </span>
            </NavLink>
          </>
        ))}
      </NavMenu>
      <DropDown />

      <SearchBox onClick={HandleSearch}>
        <Search />
      </SearchBox>
      {SetSearchisOpen && (
        <SearchArea Navbar={Navbar} SearchIsopen={SearchIsopen} />
      )}
    </Container>
  );
}
const Container = styled.div`
  width: 100%;
  height: 60px;
  background: ${({ navbar }) =>
    navbar ? "rgba(255,255,255,0.9)" : "transparent"};
  ${"" /* border-top: 5px solid red; */}
  display: grid;
  grid-template-columns: ${({ search }) =>
    search ? "auto 1fr 1fr 50px" : "auto 1fr  50px"};
  color: ${({ navbar }) => (navbar ? "#242424" : "#fff")};

  position: fixed;
  align-items: center;
  over-flow: hidden;
  ${
    "" /* border-bottom: 1px solid ${({ navbar }) => (navbar ? "green" : "transparent")}; */
  }

  ${"" /* border-bottom: 1px solid #32526e; */}
  z-index: 999;
  @media (max-width: 1010px) {
    grid-template-columns: auto 50px 50px;
  }
`;
const Logo = styled.div`
  padding: 0 10px;
  h1 {
    margin: 0;
  }
`;
const Menubar = styled(FiMenu)`
  display: none;
  @media (max-width: 1010px) {
    display: block;
  }
`;
const Navlink = css`
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  padding: 16px 14px 13px;
  font-size: 19px;
  text-transform: uppercase;
  &:hover {
    borer: 1px solid red;
  }

  ${"" /* font-weight: 700; */}
`;
const NavMenu = styled.div`
  display: flex;
  justify-content: flex-end;
  padding: 0 5px;
  @media (max-width: 1010px) {
    display: none;
  }
`;
const NavLink = styled(Link)`
  ${Navlink}
  flex-wrap:nowrap;
  color: ${({ navbar }) => (navbar ? "#242424" : "#fff")};
  position: relative;
  margin-left: 5px;
  font-size: 20px;
  &:before {
    display: block;
    background: red;
    content: "";
    width: 50%;
    height: 2px;
    position: absolute;
    bottom: 0px;
    transform-orgin: left center;
    transform: scaleX(0);
    transition: all 250ms cubic-bezier(0.25, 0.45, 0.46, 0.74);
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    &:before {
      opacity: 1;
      visibility: visible;
      transform: scaleX(1);
    }
  }
  @media (max-width: 1240px) {
    font-size: 15px;
  }
  span {
    font-size: 17px;
    margin-left: 5px;
  }
`;
const SearchBox = styled.div`
  background: #bc1f60;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const Search = styled(SearchIcon)`
  color: white;
`;
const SearchInput = styled.div`
  width: 100%;

  input {
    width: 100%;
  }
`;
export default Navbar;
