import React from "react";
import styled, { css } from "styled-components";
import { AiOutlineInstagram } from "react-icons/ai";
import { FaFacebookF } from "react-icons/fa";
import { AiOutlineMail } from "react-icons/ai";
import { AiFillLinkedin } from "react-icons/ai";
import { AiOutlineTwitter } from "react-icons/ai";

import { Link } from "react-router-dom";
import { Container } from "@material-ui/core";

function footer() {
  return (
    <FooterContainer>
      <Footerone>
        <Instagram />
        <Facebook />
        <Twitter />
        <Mail />
        <Linkdin />
      </Footerone>
      <FooterEnd>
        <span>&copy;copyright reserved</span>
        <span>Desinged by Arun Prabhuk</span>
      </FooterEnd>
    </FooterContainer>
  );
}
const FooterContainer = styled.section`
  overflow: hidden;
  height: auto;
  display: flex;
  flex-direction: column;
  background: black;
  align-items: center;
  justify-content: center;
`;

const ThreeFooter = css`
  display: flex;
  align-items: center;
  text-transform: lowercase;
  width: 100%;
  color: #fff;
  max-width: 300px;
`;
const Footerone = styled.div`
  ${ThreeFooter}
  justify-content: space-evenly;
  flex-direction: row;
`;

const IconColor = css`
  color: white;
  font-size: 20px;
  padding: 10px;
`;

const Twitter = styled(AiOutlineTwitter)`
  ${IconColor}
`;
const Instagram = styled(AiOutlineInstagram)`
  ${IconColor}
`;
const Facebook = styled(FaFacebookF)`
  ${IconColor}
`;
const Mail = styled(AiOutlineMail)`
  ${IconColor}
`;
const Linkdin = styled(AiFillLinkedin)`
  ${IconColor}
`;
const FooterEnd = styled.div`
  display: flex;
  color: white;
  width: 100%;
  align-items: center;
  justify-content: center;
  span {
    font-size: 10px;
    margin-right: 15px;
  }
`;

export default footer;
