import styled from "styled-components";
import { NavData } from "../Data/NavData";
import { FaTimes } from "react-icons/fa";
import { Link } from "react-router-dom";
import { useState } from "react";

function MobileDropDown({ isOpen, Toggle }) {
  // cursor finction start
  const [cursorX, setcursorX] = useState();
  const [cursorY, setcursorY] = useState();

  window.addEventListener("mousemove", (e) => {
    setcursorX(e.pageX);
    setcursorY(e.pageY);
  });

  // cursor function End
  return (
    <DropDownWrappr isOpen={isOpen} onClick={Toggle}>
      <Icon>
        <CloseIcon onClick={Toggle} />
      </Icon>
      <DropDownMenu>
        {NavData.map((item, index) => (
          <>
            <DropMenuItems key={index} to={item.path}>
              {item.title}
              <CursorEffect
                style={{ top: cursorY + "px", left: cursorX + "px" }}
              ></CursorEffect>
            </DropMenuItems>
          </>
        ))}
      </DropDownMenu>
    </DropDownWrappr>
  );
}
const DropDownWrappr = styled.div`
  height: 100vh;
  width: 100%;
  position: fixed;
  background: #000000;
  top: ${({ isOpen }) => (isOpen ? "0" : "-100%")};
  left: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: ${({ isOpen }) => (isOpen ? "1" : "0")};
  transition: 0.3s ease-in-out;
`;
const Icon = styled.div`
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: #fff;
`;
const CloseIcon = styled(FaTimes)`
  font-size: 25px;
`;

const DropDownMenu = styled.div`
  display: grid;
`;
const CursorEffect = styled.div`
  position: fixed;
  width: 20px;
  height: 20px;
  background: transparent;
  border-radius: 50%;
  pointer-events: none;
  mix-blend-mode: difference;
  transform: scale(1);
`;
const DropMenuItems = styled(Link)`
  text-decoration: none;
  font-size: 3rem;
  color: #fff;
  text-transform: capitalize;

  &:hover {
    ${CursorEffect} {
      transform: scale(5);
      background: white;
    }
  }

  @media (max-width: 600px) {
    font-size: 2rem;
  }
`;

export default MobileDropDown;
