import React from "react";
import SearchIcon from "@material-ui/icons/Search";
import styled from "styled-components";
function Search({ SearchIsopen, Navbar }) {
  return (
    <SearchContainer isopen={SearchIsopen}>
      <input type="text" placeholder="type than hit enter to search" />
    </SearchContainer>
  );
}
const SearchContainer = styled.div`
  position: absolute;
  top: 56px;
  right: ${({ isopen }) => (isopen ? "75px" : "-100%")};
  width: 300px;
  height: 50px;
  transition: 0.3s ease-in-out;
  border-radius: 50px;
  margin: 5px;

  input {
    min-width: 100%;
    border: none;
    outline: none;
    height: 100%;
    padding: 0 3rem;
    position: relative;
    ${"" /* border-left: 2px solid red; */}
    font-size: 17px;
    background: white;
    border-radius: 20px;

    ${
      "" /* &:hover {
      background: red;
    } */
    };
  }
`;
const Serach = styled(SearchIcon)`
  position: absolute;
`;
export default Search;
