import axios from "axios";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import styled from "styled-components";
import Pagination from "./Components/Pagination";
import Users from "./Components/Users";
import { USER_PER_PAGE } from "./utiliti/constants";
function RegionContent({ icon, title, isred }) {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);

  useEffect(() => {
    const fetchUsers = async () => {
      setLoading(true);
      const res = await axios.get(
        "https://randomuser.me/api/?page=1&results=50&nat=us"
      );
      setLoading(false);
      setUsers(res.data.results);

      setTotalPages(Math.ceil(res.data.results.length / USER_PER_PAGE));
    };
    fetchUsers();
  }, []);

  const handleClick = (num) => {
    setPage(num);
  };
  return (
    <RegionContainer isred={isred}>
      <Heading>
        <span>{icon}</span>
        <h1>{title}</h1>
      </Heading>
      <RegionWrapper>
        {loading ? (
          <p>Loading...</p>
        ) : (
          <>
            <Users users={users} page={page} />
            <Pagination totalPages={totalPages} handleClick={handleClick} />
          </>
        )}
      </RegionWrapper>
    </RegionContainer>
  );
}
const RegionContainer = styled.section`
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
    rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
  height: auto;
  border-top: 2px solid ${({ isred }) => (isred ? "red" : "green")};
  background: aliceblue;
  @media (max-width: 500px) {
    box-shadow: rgb(0 0 0 / 6%) 0px 1px 3px 0px,
      rgb(0 0 0 / 15%) 0px 1px 5px 0px;
  }
`;
const Heading = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 20px;
  h1 {
    margin: 0;
    font-size: 20px;
    font-weight: 100;
  }
  span {
    font-size: 3rem;
    color: #bc1f60;
  }
`;
const RegionWrapper = styled.div`
  display: grid;
  grid-template-rows: 1fr;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin: 10px;
`;

export default RegionContent;
